<template>
    <div>
        <b-table
                hover v-if="majice.length"
                sticky-header="800px"
                :items="majice"
                :fields="fields"
                head-variant="dark"
                @row-clicked="EditMajica">
            <template v-if="admin==false" v-slot:cell(delete)="row">
                <b-button variant="danger" @click="delete_majica(row.item.id)">Delete</b-button>
            </template>
        </b-table>
        <h1 v-else>Nema majica</h1>
    </div>
</template>

<script>
    import router from "@/router";
    import { mapState, mapActions } from 'vuex';

    export default {
        name: "MajicaList",
        computed: {
            ...mapState(['majice', 'admin'])

        },
        data() {
            
            return {
                fields: [
                    { key: 'marka' },
                    { key: 'delete' }
                ]
            }
           
        },
        methods: {
            ...mapActions(['delete_majica']),

            EditMajica: function (item, index, event) {
                router.push({path: `/majica/${item.id}`})
            }
        }
    }
</script>

<style>
    tr:hover td{
        background:brown;
    }
</style>