<template>
    <div>
         <router-link to="/home">Home</router-link>
        <router-link to="/">Logout</router-link>
       <div  v-if="admin==false">
        <router-link to="/edit">NovaPatika</router-link>
       </div>
        <Header/>
        <b-container>
            <b-row>
                <b-col cm="6" >
                    <div v-if="edit">
                        <EditPatika :marka="patika.marka" :model="patika.model" :cena="patika.cena"/>
                    </div>
                    <div v-else>
                        <ShowPatika :patika="patika"/>
                    </div>
                </b-col>
            </b-row>
            <b-row v-if="admin==false">
                <b-col cm="2" style="margin-top: 10px">
                    <b-button variant="primary" size="lg" @click="toggleEdit" v-html="edit ? 'Cancel' : 'Edit'"/>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
    import EditPatika from "@/components/EditPatika";
    import Header from "@/components/Header";
    import ShowPatika from "@/components/ShowPatika";
    import { mapState, mapActions } from 'vuex';

    export default {
        name: 'Patika',
        components: {
            Header,
            EditPatika,
            ShowPatika
        },
        data() {
            return {
                edit: false
            }
        },
        computed: {
            ...mapState(['patike','admin']),

            patika: function () {
                for (let i = 0; i < this.patike.length; i++)
                    if (this.patike[i].id === parseInt(this.$route.params.id))
                        return this.patike[i];
            }
        },
        methods: {
            ...mapActions(['load_patike']),

            toggleEdit: function () {
                this.edit = !this.edit
            }
        }
    }
</script>

