import Vue from 'vue'
import Vuex from 'vuex'
import router from "../router" 

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    patike: [],
    majice: [],
    admin: false
  },

  mutations: {

    set_admin: function (state, admin){
      state.admin = admin;
    },

    set_patike: function (state, patike) {
      state.patike = patike;
    },

    set_patike: function (state, patike) {
      state.patike = patike;
    },

    add_patika: function (state, patike) {
      state.patike.push(patike);
    },

    remove_patika: function (state, id) {
      for (let m = 0; m < state.patike.length; m++) {
        if (state.patike[m].id === id) {
          state.patike.splice(m, 1);
          break;
        }
      }
    },

    update_patika: function (state, payload) {
      for (let m = 0; m < state.patike.length; m++) {
        if (state.patike[m].id === parseInt(payload.id)) {
          state.patike[m].marka = payload.patika.marka;
          state.patike[m].model = payload.patika.model;
          state.patike[m].cena = payload.patika.cena;
          break;
        }
      }
    },
    set_majice: function (state, majice) {
      state.majice = majice;
    },

    set_majice: function (state, majice) {
      state.majice = majice;
    },

    add_majica: function (state, majice) {
      state.majice.push(majice);
    },

    remove_majica: function (state, id) {
      for (let m = 0; m < state.majice.length; m++) {
        if (state.majice[m].id === id) {
          state.majice.splice(m, 1);
          break;
        }
      }
    },

    update_majica: function (state, payload) {
      for (let m = 0; m < state.majice.length; m++) {
        if (state.majice[m].id === parseInt(payload.id)) {
          state.majice[m].marka = payload.majica.marka;
          state.majice[m].cena = payload.majica.cena;
          break;
        }
      }
  },
},

  actions: {
    load_patike: function ({ commit }) {
      fetch('http://localhost:8080/api/patike', { method: 'get' }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json()
      }).then((jsonData) => {
        commit('set_patike', jsonData)
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    load_majice: function ({ commit }) {
      fetch('http://localhost:8080/api/majice', { method: 'get' }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json()
      }).then((jsonData) => {
        commit('set_majice', jsonData)
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },


    login : function ({commit}, user){
      fetch('http://localhost:8080/api/login', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: user
      }).then((response) => {
        if (!response.ok)
          throw response;
        router.push("/home");
        return response.json();
      });
    },
    isadmin: function ({ commit }) {
      fetch('http://localhost:8080/api/isadmin', { method: 'get' }).then((response) => {
        if (!response.ok)
          throw response;
        return response.json();
      }).then((jsonData) => {
        commit('set_admin', jsonData)
      });
    },
    newUser : function ({commit}, user){
      fetch('http://localhost:8080/api/register', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: user
      }).then((response) => {
        if (!response.ok)
          throw response;
        router.push("/");
        return response.json();
      });
    },

    delete_patika: function({ commit }, id) {
      fetch('http://localhost:8080/api/delpatika/' + id, { method: 'get' }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json()
      }).then((jsonData) => {
        commit('remove_patika', jsonData.id)
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    new_patika: function({ commit }, patika) {
      fetch('http://localhost:8080/api/addpatika', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: patika
      }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json();
      }).then((jsonData) => {
        commit('add_patika', jsonData);
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    delete_majica: function({ commit }, id) {
      fetch('http://localhost:8080/api/delmajica/' + id, { method: 'get' }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json()
      }).then((jsonData) => {
        commit('remove_majica', jsonData.id)
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    new_majica: function({ commit }, majica) {
      fetch('http://localhost:8080/api/addmajica', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: majica
      }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json();
      }).then((jsonData) => {
        commit('add_majica', jsonData);
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    change_majica: function({ commit }, payload) {
      fetch('http://localhost:8080/api/updatemajica/' + payload.id, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: payload.majica
      }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json();
      }).then((jsonData) => {
        commit('update_majica', {id: payload.id, majica:jsonData});
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    },

    change_patika: function({ commit }, payload) {
      fetch('http://localhost:8080/api/updatepatika/' + payload.id, {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: payload.patika
      }).then((response) => {
        if (!response.ok)
          throw response;

        return response.json();
      }).then((jsonData) => {
        commit('update_patika', {id: payload.id, patika:jsonData});
      }).catch((error) => {
        if (typeof error.text === 'function')
          error.text().then((errorMessage) => {
            alert(errorMessage);
          });
        else
          alert(error);
      });
    }
  }
})
