import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Patika from "@/views/Patika";
import NovaPatika from "@/views/NovaPatika";
import Majica from "@/views/Majica";
import NovaMajica from "@/views/NovaMajica";
import Register from "@/views/Register";
import Login from "@/views/Login";

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/edit',
    name: 'NovaPatika',
    component: NovaPatika
  },
  {
    path: '/patika/:id',
    name: 'patika',
    component: Patika
  },
  {
    path: '/editm',
    name: 'NovaMajica',
    component: NovaMajica
  },
  {
    path: '/majica/:id',
    name: 'majica',
    component: Majica
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/register',
    name: 'register',
    component: Register
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
