<template>
    <b-container fluid>
        <b-form>
            <b-row class="mt-2">
                <b-col sm="2" offset="2">
                    <b-input v-model="newMarka" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Marka"></b-input>
                </b-col>

            </b-row>

            <b-row class="mt-2">
                <b-col sm="2" offset="2">
                    <b-input v-model="newCena" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Cena"></b-input>
                </b-col>

            </b-row>

          <b-row class = "mt-2">
            <b-col sm="1">
              <b-button variant="primary" size="lg" @click="addNew">Save</b-button>
            </b-col>
          </b-row>
        </b-form>
    </b-container>
</template>

<script>
    import { mapActions } from 'vuex';


    export default {
        name: "EditMajica",
        props: {
            marka: {
                type: String,
                default: ''
            },
            cena: {
                type: String,
                default: ''
            }
        },
        data() {
            return {
              newMarka: '',
              newCena: ''
            }
        },
        mounted: function () {
            this.newMarka = this.marka;
            this.newCena = this.cena;
            
        },
        methods: {
            ...mapActions(['new_majica', 'change_majica']),

            addNew: function() {
              const Joi = require('joi');

              const sema1 = Joi.object().keys({
                marka: Joi.string().trim().required(),
                cena :Joi.string().trim().required(),
              });

              const link = Joi.object().keys({
                id: Joi.number().min(1).max(50).required()
              });
                const majica = JSON.stringify({marka: this.newMarka, cena: this.newCena});
                let { error } = Joi.validate(majica, sema1);
                if(error){
                    alert(error.details[0].message);
                  return;
                }

                if (!this.$route.params.id)
                    this.new_majica(majica);
                else
                    this.change_majica({id: this.$route.params.id, majica: majica});

                this.newMarka = '';
                this.newCena = '';
            }
        }
    }
</script>

