<template>
  <div id="Register">
    <h1>Register</h1>
    <input type="text" name="username" v-model ="username" placeholder="Select username" />
    <input type="password" name="password" v-model="password" placeholder="Select password" />
    <input type="text" name="mail" v-model ="mail" placeholder="Select mail" />
    <input type="text" name="name" v-model ="name" placeholder="Select name" />
    <input type="text" name="surname" v-model ="surname" placeholder="Select surname" />
    <b-button variant="primary" size="lg" @click="Register"> Register </b-button>
  </div>
</template>

<script>


import { mapActions } from 'vuex';


    export default {
        name: "Register",
        props: {
            username: {
                type: String,
                default: ''
            },
            password: {
                type: String,
                default: ''
            },
            mail: {
                type: String,
                default: ''
            },
            name: {
                type: String,
                default: ''
            },
            surname: {
                type: String,
                default: ''
            },
        },
        methods: {
            ...mapActions(['newUser']),

            Register: function() {
              const Joi = require('joi');

              const sema = Joi.object().keys({
                username: Joi.string().trim().required(),
                password: Joi.string().trim().required(),
                name: Joi.string().trim().required(),
                surname: Joi.string().trim().required(),
                mail: Joi.string().trim().required(),
              });

              const link = Joi.object().keys({
                id: Joi.number().min(1).max(50).required()
              });
                const user = JSON.stringify({username: this.username, password: this.password, name: this.name, surname: this.surname, mail: this.mail});
                let { error } = Joi.validate(user, sema);

                if(error){
                    alert(error.details[0].message);
                  return;
                }

                

                this.newUser(user);

            }
        }
    }


</script>

