<template>
    <b-container fluid>
        <b-form>
            <b-row class="mt-2">
                <b-col sm="2" offset="2">
                    <b-input v-model="newMarka" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Marka"></b-input>
                </b-col>

              <b-col sm="2" offset="2">
                <b-input v-model="newModel" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Model"></b-input>
              </b-col>
            </b-row>

            <b-row class="mt-2">
                <b-col sm="2" offset="2">
                    <b-input v-model="newCena" class="mb-2 mr-sm-2 mb-sm-0" placeholder="Cena"></b-input>
                </b-col>

            </b-row>

          <b-row class = "mt-2">
            <b-col sm="1">
              <b-button variant="primary" size="lg" @click="addNew">Save</b-button>
            </b-col>
          </b-row>
        </b-form>
    </b-container>
</template>

<script>
    import { mapActions } from 'vuex';


    export default {
        name: "EditPatika",
        props: {
            marka: {
                type: String,
                default: ''
            },
            model: {
                type: String,
                default: ''
            },
            cena: {
                type: String,
                default: ''
            }
        },
        data() {
            return {
              newMarka: '',
              newModel: '',
              newCena: ''
            }
        },
        mounted: function () {
            this.newMarka = this.marka;
            this.newModel = this.model;
            this.newCena = this.cena;
        },
        methods: {
            ...mapActions(['new_patika', 'change_patika']),

            addNew: function() {
              const Joi = require('joi');

              const sema = Joi.object().keys({
                marka: Joi.string().trim().required(),
                model: Joi.string().trim().required(),
                cena :Joi.string().trim().required(),
              });

              const link = Joi.object().keys({
                id: Joi.number().min(1).max(50).required()
              });
                const patika = JSON.stringify({marka: this.newMarka, model: this.newModel, cena: this.newCena});
                let { error } = Joi.validate(patika, sema);

                if(error){
                    alert(error.details[0].message);
                  return;
                }

                if (!this.$route.params.id)
                    this.new_patika(patika);
                else
                    this.change_patika({id: this.$route.params.id, patika: patika});

                this.newMarka = '';
                this.newModel = '';
                this.newCena = '';
            }
        }
    }
</script>

