<template>
    <div>
         <router-link to="/home">Home </router-link>
        <router-link to="/">Logout</router-link>
       <div  v-if="admin==false">
         <router-link to="/edit">Nova Patika </router-link>
          <router-link to="/editm">Nova Majica </router-link>
       </div>
        <Header/>
        <b-container>
            <b-row>
                <b-col cm="6" >
                    <div v-if="edit">
                        <EditMajica :marka="majica.marka" :cena="majica.cena"/>
                    </div>
                    <div v-else>
                        <ShowMajica :majica="majica"/>
                    </div>
                </b-col>
            </b-row>
            <b-row v-if="admin==false">
                <b-col cm="2" style="margin-top: 10px">
                    <b-button variant="primary" size="lg" @click="toggleEdit" v-html="edit ? 'Cancel' : 'Edit'"/>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
    import EditMajica from "@/components/EditMajica";
    import Header from "@/components/Header";
    import ShowMajica from "@/components/ShowMajica";
    import { mapState, mapActions } from 'vuex';

    export default {
        name: 'Patika',
        components: {
            Header,
            EditMajica,
            ShowMajica
        },
        data() {
            return {
                edit: false
            }
        },
        computed: {
            ...mapState(['majice','admin']),

            majica: function () {
                for (let i = 0; i < this.majice.length; i++)
                    if (this.majice[i].id === parseInt(this.$route.params.id))
                        return this.majice[i];
            }
        },
        methods: {
            ...mapActions(['load_majice']),

            toggleEdit: function () {
                this.edit = !this.edit
            }
        }
    }
</script>

