<template>
  <div id="Login">
    <h1>Login</h1>
    <input type="text" name="username" v-model ="username" placeholder="Username" />
    <input type="password" name="password" v-model="password" placeholder="Password" />
    <b-button variant="primary" size="lg" @click="Login"> Login </b-button>
    <router-link to="/register">Register</router-link>
  </div>
</template>

<script>
import { mapActions } from 'vuex';


    export default {
        name: "Login",
        props: {
            username: {
                type: String,
                default: ''
            },
            password: {
                type: String,
                default: ''
            },
        },
        methods: {
            ...mapActions(['login','isadmin']),

            Login: function() {
              const Joi = require('joi');

              const sema = Joi.object().keys({
                username: Joi.string().trim().required(),
                password: Joi.string().trim().required()
              });

              const link = Joi.object().keys({
                id: Joi.number().min(1).max(50).required()
              });
                const user = JSON.stringify({username: this.username, password: this.password});
                let { error } = Joi.validate(user, sema);

                if(error){
                    alert(error.details[0].message);
                  return;
                }

               
                this.login(user);
                this.isadmin();

            }
        }
    }

</script>

